import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CFormLabel,
  CCardFooter,
  CModal,
  CModalHeader,
  CModalFooter,
  CModalTitle,
  CModalBody,
  CFormCheck,
  CFormSelect,
  CFormInput,
} from "@coreui/react-pro";
import React, { useEffect, useState,useNavigate } from "react";

import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import {
  faTimesCircle,
  faCheckCircle,
  faArrowRight,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Formik, Form, Field, FieldArray, ErrorMessage } from "formik";
import * as Yup from "yup";
import Loader from "src/loader/Loader";
import EVFormikControl from "src/ev_formik_controls/EVFormikControl";
import EDU_AXIOS from "src/helpers/edu_axios";
import notify from "src/helpers/notify";
import { Link } from "react-router-dom";

const add_holiday = (props) => {
  let school_id = props.match.params.school_id;
  
  let [visible, setVisible] = useState(false);
  let [days, setDays] = useState(0);
  let [sDate, setSDate] = useState("");
  let [eDate, setEDate] = useState("");
  let [type,setType]=useState("");
  let [name,setName]=useState("");
  // holiday type
  let h_type = [
    { text: "Holiday", value: "holiday" },
    { text: "Exam Calender", value: "exam_calender" },
    { text: "Summer Holiday", value: "summer_holiday" },
    { text: "Winter Holiday", value: "winter_holiday" },
  ];
  // open cancle modal box
  let openModalBox = () => {
    setTimeout(() => setVisible(true), 0);
  };

  // close cancle modal box
  let closeModalBox = () => {
    setTimeout(() => setVisible(false), 0);
  };



  let checksubmit= ()=>{
    alert(days)
  }

  // gate today
  let getToday = () => {
    let today = new Date();
    let dd = String(today.getDate()).padStart(2, "0");
    let mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    let yyyy = today.getFullYear();
    today = yyyy + "-" + mm + "-" + dd;
    return today;
  };

  // gate tomorrow
  let getTomorrow = () => {
    let tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    let dd = String(tomorrow.getDate()).padStart(2, "0");
    let mm = String(tomorrow.getMonth() + 1).padStart(2, "0"); //January is 0!
    let yyyy = tomorrow.getFullYear();
    tomorrow = yyyy + "-" + mm + "-" + dd;
    return tomorrow;
  };

  // start date value
  let sDateVal = (e) => {
    setTimeout(() => setSDate(e.target.value), 0);
  };
  // end date value
  let eDateVal = (e) => {
    setTimeout(() => setEDate(e.target.value), 0);
  };

  // aData
  let aDate = (sDate, eDate) => {
    if (sDate != "" && eDate != "") {
      let start = sDate.split("-").reverse();
      start = start[1] + "/" + start[0] + "/" + start[2];
      let end = eDate.split("-").reverse();
      end = end[1] + "/" + end[0] + "/" + end[2];
      var date1 = new Date(start);
      var date2 = new Date(end);
      const diffTime = Math.abs(date2 - date1);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      setDays(diffDays + 1);
    } else {
      setDays(0);
    }
  };
  // formik params
  let initialValues = {
    type: "",
    name: "",
    start_date: sDate,
    end_date: eDate,
    days: days,
  };

  // validation rules of the form fields
  const validationSchema = Yup.object().shape({
    type: Yup.string().required("Holiday type is required"),
    name: Yup.string().required("Holiday name is required"),
    start_date: Yup.string().required("Start date is required"),
    end_date: Yup.string().required("End date is required"),
    days: Yup.string().required("Number of days is required"),
  });
  // submit form
  const onSubmit = async () => {
    await EDU_AXIOS.post(
      process.env.REACT_APP_API_PATH + "/cm/create-calender",
      {
        school_id: school_id,
        type: type,
        name: name,
        start: sDate,
        end: eDate,
        duration: days,
      }
    )
      .then(async (res) => {
        // resetForm({ values: "" });
        notify("success", res.data.message);
      
      })
      .catch((err) => {
        notify("error", err);
      });
  };

  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          <CRow>
            <CCol md="8" lg="6" xl="8">
              <h5>Add Holiday</h5>
            </CCol>
          </CRow>
          <p className="text-muted">Create Holiday List</p>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
            enableReinitialize={true}
          >
            {(formik) => (
              <Form className="row g-3">
                <CCol md={6}>
                  {/* <CFormLabel htmlFor="type" className="required-field">
                    Type
                  </CFormLabel>
                  <EVFormikControl
                    control="selectwithkey"
                    name="type"
                    id="type"
                    placeholder="Select holiday type..."
                    options={h_type}
                  /> */}
                   {/* let h_type = [
    { text: "Holiday", value: "holiday" },
    { text: "Exam Calender", value: "exam_calender" },
    { text: "Summer Holiday", value: "summer_holiday" },
    { text: "Winter Holiday", value: "winter_holiday" },
  ]; */}
   <CFormLabel htmlFor="type" className="required-field">
                    Type
                  </CFormLabel>
                  <CFormSelect name="type" value={type} id="type" onChange={(e)=>setType(e.target.value)} placeholder="Please select any one type">
                    <option value="">Please select any one type</option>
                    <option value="Holiday">Holiday</option>
                    <option value="exam_calender">Exam Calendar</option>
                    <option value="summer_holiday">Summer Holiday</option>
                    <option value="winter_holiday">Winter Holiday</option>
                  
                  </CFormSelect>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="name" className="required-field">
                    Name
                  </CFormLabel>
                  {/* <EVFormikControl
                    control="input"
                    type="text"
                    name="name"
                    id="name"
                    placeholder="Holiday Name"
                    value={name}
                 onChange={(e)=>setName(e.target.value)}
                  /> */}
                  <CFormInput
                   name="name"
                   id="name"
                   placeholder="Holiday Name"
                   value={name}
                onChange={(e)=>setName(e.target.value)}
                  >

                  </CFormInput>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="start" className="required-field">
                    Start Date
                  </CFormLabel>
                  <EVFormikControl
                    control="input"
                    type="date"
                    name="start_date"
                    id="start_date"
                    min={getToday()}
                
                    onChange={(e) => {
                      sDateVal(e);
                      aDate(e.target.value, eDate);
                    }}
                  />
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="end" className="required-field">
                    End Date
                  </CFormLabel>
                  <EVFormikControl
                    control="input"
                    type="date"
                    name="end_date"
                    id="end_date"
                    min={getToday()}
                
                    onChange={(e) => {
                      eDateVal(e);
                      aDate(sDate, e.target.value);
                    }}
                  />
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="days" className="required-field">
                    Number of Days
                  </CFormLabel>
                  <EVFormikControl
                    control="input"
                    type="number"
                    name="days"
                    id="days"
                    value={days}
                    placeholder="Number of Days"
                  />
                </CCol>
                <CCardFooter>
                  <CButton block color="light" onClick={openModalBox}>
                    <FontAwesomeIcon icon={faTimesCircle} /> Cancel
                  </CButton>
                {
                  eDate!='' && sDate!='' && type!='' && name!='' && days!=''?
<CButton
                    block
                    color="dark"
                    type="submit"
                    onClick={onSubmit}
                    
                    style={{ marginLeft: "15px" }}
                  >
                    {formik.isSubmitting ? (
                      <Loader />
                    ) : (
                      <>
                        <FontAwesomeIcon icon={faSave} /> Save
                      </>
                    )}
                  </CButton>
                  :
<CButton
                    block
                    color="dark"
                    type="submit"
                    onClick={onSubmit}
                    disabled
                    style={{ marginLeft: "15px" }}
                  >
                    {formik.isSubmitting ? (
                      <Loader />
                    ) : (
                      <>
                        <FontAwesomeIcon icon={faSave} /> Save
                      </>
                    )}
                  </CButton>
                }  
                </CCardFooter>
                <CModal visible={visible} onDismiss={closeModalBox}>
                  <CModalHeader onDismiss={closeModalBox}>
                    <CModalTitle>
                      <h5>Cancel</h5>
                    </CModalTitle>
                  </CModalHeader>

                  <CModalBody>Do You Want To Cancel ?</CModalBody>
                  <CModalFooter>
                    <CButton color="light" onClick={closeModalBox}>
                      <FontAwesomeIcon icon={faTimesCircle} /> No
                    </CButton>
                    <Link to="/">
                      <CButton color="dark">
                        <FontAwesomeIcon icon={faCheckCircle} /> Yes
                      </CButton>
                    </Link>
                  </CModalFooter>
                </CModal>
              </Form>
            )}
          </Formik>
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
};

export default add_holiday;
